/* Third party */
@import "../../bower_components/normalize.css/normalize.css";
/* Custom */
/***************************************************/
/* Set custom or change third-party Less variables */
/***************************************************/
/* Third party */
/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
/*
These "retina group" variables are mappings for the naming and pairing of normal and retina sprites.

The list formatted variables are intended for mixins like `retina-sprite` and `retina-sprites`.
*/
/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}
*/
/*
The `retina-sprite` class sets up rules and a media query for a sprite/retina sprite.
  It should be used with a "retina group" variable.

The media query is from CSS Tricks: https://css-tricks.com/snippets/css/retina-display-media-query/

@icon-home-group: 'icon-home' @icon-home @icon-home-2x;

.icon-home {
  .retina-sprite(@icon-home-group);
}
*/
/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
/*
The `.retina-sprites` mixin generates a CSS rule and media query for retina groups
  This yields the same output as CSS retina template but can be overridden in LESS

.retina-sprites(@retina-groups);
*/
.png-sprite:before {
  content: '';
  display: inline-block;
}
.svg-sprite:before {
  content: '';
  display: inline-block;
  width: 32px!important;
  height: 32px!important;
  background-size: contain!important;
}
.roboto-condensed {
  font-family: 'Roboto Condensed', sans-serif;
}
* {
  font-family: 'Roboto Condensed', sans-serif;
}
* {
  box-sizing: border-box;
}
html,
body {
  font-size: 100%;
}
@media (max-width: 1400px) {
  html,
  body {
    font-size: 70%;
  }
}
@media (max-width: 1100px) {
  html,
  body {
    font-size: 65%;
  }
}
@media (max-height: 850px) {
  html,
  body {
    font-size: 65%;
  }
}
.container {
  width: 100vw;
  height: 100vh;
  min-height: 768px;
  min-width: 1024px;
  margin: 0 auto;
  clear: both;
  position: relative;
  overflow: hidden;
}
.container:before {
  content: '';
  display: table;
}
.card {
  width: 100%;
  height: 100%;
  float: left;
  background-color: #ffffff;
}
.card--flip-back {
  background-color: #5a5a5a;
  top: 0;
}
.card__header {
  width: 90%;
  height: 123px;
  background-color: #024b9d;
  margin: 0 auto;
  text-align: center;
  display: flex;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.3);
  z-index: 100;
  position: relative;
  top: 48px;
}
.card__header--2 {
  opacity: .9;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.4);
}
.card__header--3 {
  opacity: .8;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.5);
}
.card__header--4 {
  opacity: .7;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.6);
}
.card__header--5 {
  opacity: .6;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.7);
}
.card__title {
  font-size: 1.75rem;
  font-weight: bold;
  line-height: 34px;
  text-transform: uppercase;
  margin: 0;
  align-self: center;
  color: #fff;
  width: 100%;
  text-align: center;
}
.card__img {
  height: 100%;
  width: 100%;
  position: absolute;
  object-fit: cover;
}
.card__list {
  list-style-type: none;
  padding-left: 3rem;
  padding-right: 0.5rem;
  margin: 0;
  position: absolute;
  top: 210px;
}
.card__list-item {
  padding: 0;
  padding-bottom: 0.8rem;
}
.card__list-link {
  text-transform: uppercase;
  font-size: 1.125rem;
  color: #fff;
  letter-spacing: .9px;
  text-decoration: none;
  transition: .5s;
}
.card__list-link--current {
  color: #3399ff;
}
.card__list-link:hover {
  color: #3399ff;
}
.footer-wrapper {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 0 auto;
  padding-top: 18px;
  overflow: hidden;
  clear: both;
  z-index: 150;
}
.footer {
  opacity: .9;
  height: 210px;
  background-color: #024b9d;
  box-shadow: 0 0px 18px 0 #000;
  padding-top: 50px;
}
.footer__main {
  clear: both;
  text-align: center;
  position: relative;
  margin-bottom: 40px;
}
.footer__contacts {
  font-size: 29px;
  font-weight: bold;
  color: #fff;
  display: inline-block;
  position: absolute;
  left: 98px;
  bottom: 0;
}
.footer__contacts--email {
  right: 113px;
  left: inherit;
}
.footer__logo {
  display: inline-block;
}
.footer__copyright {
  text-align: center;
  opacity: .25;
  color: #fff;
}
.flip {
  perspective: 700;
  position: relative;
  width: 20%;
  height: 100%;
  float: left;
  min-height: 768px;
  z-index: 50;
}
.flip:hover .flip__box {
  transform: rotateY(-90deg);
}
.flip__box {
  transform-style: preserve-3d;
  transition: all 0.5s ease-out;
  transform-origin: 90px 90px -90px;
  top: -123px;
  width: 100%;
  height: 100%;
  position: relative;
}
.flip__front {
  backface-visibility: hidden;
  position: absolute;
  transform-origin: 0 0;
  transform: rotateY(0);
  width: 100%;
  height: 100%;
}
.flip__back {
  backface-visibility: hidden;
  position: absolute;
  transform-origin: 0 0;
  transform: rotateY(90deg);
  width: 100%;
  height: 100%;
  left: 18rem;
}
.flip--inner > .card {
  cursor: auto;
}
.flip--inner .flip__front {
  display: none;
}
.flip--inner .flip__back {
  display: block;
  transform: none;
}
.flip--inner:hover .flip__front {
  transform: none;
  display: none;
}
.flip--inner:hover .flip__back {
  transform: none;
}
.content {
  box-sizing: border-box;
  float: left;
  width: 77vw;
  height: 100vh;
  padding-left: 40px;
  padding-top: 48px;
}
.content__text {
  font-size: 1.75rem;
}
